ntent:space-between;align-items:center;margin-bottom:8px}.contact-edit form .flex-container{display:flex;justify-content:flex-end}.contact-edit form .flex-container button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:#ddd;padding:3px 7px;border-radius:5px;margin-top:5px}.contact-edit form .flex-container button:hover{background-color:#cccccccc}.signup{display:flex;flex-direction:column;align-items:center;padding:80px 0}.signup img{margin-bottom:10px}.signup img:hover{animation:animate 1.2s ease-in-out forwards}@keyframes animate{0%{transform:translateY(0%);transform:rotateY(0deg)}50%{transform:translateY(-50%) rotateY(180deg)}100%{transform:translateY(0%);transform:rotateY(0deg)}}.signup label{margin-bottom:10px}.signup input{margin-bottom:10px;padding:3px 5px}.signup button{background-color:#ddd;padding:5px 7px;border-radius:5px}.signup button:hover{background-color:#cccccccc}.statistic{padding:18px}.statistic h1{margin-bottom:20px;margin-left:45px}.statistic h3{margin-left:45px}

