.contact-edit {
	padding: 18px;

	.flex-container {
		display: flex;
		justify-content: center;
		margin-bottom: 20px;

		img {
			width: 130px;
			height: 130px;
		}
	}

	.actions {
		display: flex;
		justify-content: space-between;
		align-items: center;

		img {
			width: 50px;
			height: 50px;
		}
	}

	form {
		label {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 8px;

			input {
				// width: 100%;
			}
		}

		.flex-container {
			display: flex;
			justify-content: flex-end;

			button {
				width: fit-content;
				background-color: #ddd;
				padding: 3px 7px;
				border-radius: 5px;
				margin-top: 5px;

				&:hover {
					background-color: #cccccccc;
				}
			}
		}
	}
}
