.transfer-fund {
	width: 450px;
	background-color: #79bae5;
	border-radius: 10px;
	margin: 10px auto;
	margin-bottom: 25px;
	padding: 20px;
	padding-top: 15px;

	h3 {
		margin-bottom: 10px;
	}

	input {
		padding: 3px 5px;
		margin-left: 5px;
		background-color: transparent;
		border: none;
		border-bottom: 2px solid #aaa;
		width: 200px;
	}

	button {
		background-color: #ccc;
		padding: 3px 5px;
		font-size: 0.9rem;
		margin-left: 15px;
		border-radius: 5px;

		&:hover {
			background-color: #cccccccc;
		}
	}
}
